import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_PRODUCTIONFREQUENCYS_PAGE,
  API_PRODUCTIONFREQUENCYS_DEL,
  API_PRODUCTIONFREQUENCYS_CREATE,
  API_PRODUCTIONFREQUENCYS_UPDATE,
  API_PRODUCTIONFREQUENCYS_DETAIL
} from 'api'
//同步
export const update = object => ({
  type: actionType.PRODUCTIONFREQUENCYS_UPDATE,
  data: object
})

// 获取 - 商品属性列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.PRODUCTIONFREQUENCYS_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_PRODUCTIONFREQUENCYS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '', list = '' } = result
      dispatch({
        type: actionType.PRODUCTIONFREQUENCYS_UPDATE,
        data: {
          page,
          size,
          total,
          tableLoading: false,
          searchResult: list
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTIONFREQUENCYS_FAIL
      })
    })
}
// 删除 - 商品属性数据
export const handleOperator = (reqData, callBack) => dispatch => {
  getData({ api: API_PRODUCTIONFREQUENCYS_DEL, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTIONFREQUENCYS_FAIL
      })
    })
}
// 获取 - 单个商品属性数据
export const getSingleDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({ api: API_PRODUCTIONFREQUENCYS_DETAIL, ...reqData })
    .then(data => {
      callBack && callBack(data)
      console.log('res data is', data)
      const { result } = data
      console.log('result ', dataFormater)
      const {
        batch = '',
        location = '',
        productTime = '',
        typeKey = '',
        typeValue = ''
      } = result
      // typeValueSourceLocation
      // typeValueSource
      let timeStart = productTime && productTime.split('~')[0]
      let timeEnd = productTime && productTime.split('~')[1]
      let typeValueName = typeValue && typeValue.split(' ')[1]
      let batchName = batch && batch.split(' ')[1]
      let typeKeyFormatter = ''
      let dataFormater = ''
      if (typeKey == 1) {
        // 渠道
        let typeValueSource = { value: typeValue, children: typeValueName }
        typeKeyFormatter = { value: typeKey, children: '渠道' }
        dataFormater = {
          ...result,
          timeStart,
          timeEnd,
          typeKey: typeKeyFormatter,
          typeValueSource,
          location: { value: `${location}`, children: `${location}` },
          batch: { value: `${batch}`, children: `${batchName}` }
        }
      } else if (typeKey == 2) {
        // 地点
        let typeValueArr = typeValue.split(',')
        let batchName = batch && batch.split(' ')[1]
        let typeValueSourceLocation = {
          value: typeValue,
          children: typeValueArr
        }
        typeKeyFormatter = { value: typeKey, children: '地点' }
        dataFormater = {
          ...result,
          timeStart,
          timeEnd,
          typeKey: typeKeyFormatter,
          typeValueSourceLocation,
          location: { value: `${location}`, children: `${location}` },
          batch: { value: `${batch}`, children: `${batchName}` }
        }
      }
      dispatch({
        type: actionType.PRODUCTIONFREQUENCYS_UPDATE,
        data: {
          modalData: dataFormater
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTIONFREQUENCYS_FAIL
      })
    })
}
// 创建/编辑 - 商品属性数据
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.PRODUCTIONFREQUENCYS_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_PRODUCTIONFREQUENCYS_UPDATE
      : API_PRODUCTIONFREQUENCYS_CREATE
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.PRODUCTIONFREQUENCYS_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.PRODUCTIONFREQUENCYS_FAIL
      })
    })
}



