import React, { PureComponent } from 'react'
import { message, Modal, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.productionFrequency }),
  mapDispatchToProps
)
export default class TableTool55 extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  /* 下载模版 */
  handleDownLoad = key => {
    const downloadData = JSON.parse(localStorage.getItem('b2bMmsDownload'))
    console.log('downloadData', downloadData)
    location.href = downloadData[key].downloadUrl
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const { handleGetList, searchParams, page, size } = this.props
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  /* 报错导出 */
  handleExprotError = () => {
    const { update } = this.props
    update({ tableLoading: false })
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    handleExport({ searchParams: { page, size, ...searchParams } }, () => {
      message.success('导出成功')
    })
  }
  // 新增
  handleAdd = () => {
    const { update } = this.props
    update({ isShowModal: true, modalTitle: '生产波次维护', modalData: '' })
  }
  // 删除
  handleOperator = () => {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator,
      selectedRowKeys
    } = this.props
    const self = this
    if (selectedRowKeys && selectedRowKeys.length > 0) {
      confirm({
        title: '温馨提示',
        content: '是否进行删除操作？',
        okText: '确认',
        width: 520,
        cancelText: '取消',
        onOk() {
          handleOperator(
            {
              data: selectedRowKeys
            },
            () => {
              const { update } = self.props
              update({ selectedRowKeys: [] })
              message.success('操作成功')
              handleGetList({ searchParams: { page, size, ...searchParams } })
            }
          )
        }
      })
    } else {
      return message.error('没有可以操作的选项')
    }
  }
  render() {
    const { menuPermissionIds = [] } = this.props
    return (
      <div className={style['table-tool']}>
      {menuPermissionIds &&
          menuPermissionIds.includes('productionFrequency_add') && (
            <Button type='primary' className={style.btn} onClick={this.handleAdd}>
            新增
          </Button>
          )}
      {menuPermissionIds &&
          menuPermissionIds.includes('productionFrequency_delete') && (
            <Button
          type='primary'
          className={style.btn}
          onClick={this.handleOperator}
        >
          删除
        </Button>
          )}

      </div>
    )
  }
}



